import * as React from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import Divider from '@mui/material/Divider';

const steps = ['Información inicial', 'Datos de registro', 'Política de privacidad'];

function InfoInicial() {
    return (
        <Box sx={{ mt: '7vh' }}>
            <Typography align='center' variant='body1' gutterBottom >
                Tenemos 3 vacantes disponibles, en distintos tipos de departamentos:
            </Typography>
            <Typography align='center' variant='subtitle1' gutterBottom >
                El puesto es de Atenci&oacute;n al cliente
            </Typography>
            <Typography align='center' variant='body1' sx={{ mt: '2vh' }} >
                Buscamos gente con ganas de trabajar y aprender, que le guste trabajar en equipo y que tenga ganas de promocionarse en la misma empresa.
            </Typography>
            <Typography align='center' variant='h6' sx={{ mt: '7vh', mb: '2vh' }}>
                REQUISITOS
            </Typography>
            <List
                sx={{
                    width: '100%',
                    bgcolor: 'background.paper',
                }}
            >
                <ListItem>
                    <ListItemIcon>
                        <CheckCircleOutlineIcon color="success" />
                    </ListItemIcon>
                    <ListItemText
                        primary="Disponibilidad INMEDIATA"
                        secondary="Con posibilidad de empezar esta misma semana."
                    />
                </ListItem>
                <Divider variant="inset" component="li" />
                <ListItem>
                    <ListItemIcon>
                        <CheckCircleOutlineIcon color="success" />
                    </ListItemIcon>
                    <ListItemText
                        primary="Trabajo a LARGO plazo"
                    />
                </ListItem>
                <Divider variant="inset" component="li" />
                <ListItem>
                    <ListItemIcon>
                        <CheckCircleOutlineIcon color="success" />
                    </ListItemIcon>
                    <ListItemText
                        primary="VARIOS TIPOS DE JORNADAS"
                        secondary="Dependiendo del departamento asignado: MEDIA JORNADA 4H - JORNADA COMPLETA 8H - JORNADA INTENSIVA 6H."
                    />
                </ListItem>
                <Divider variant="inset" component="li" />
                <ListItem>
                    <ListItemIcon>
                        <CheckCircleOutlineIcon color="success" />
                    </ListItemIcon>
                    <ListItemText
                        primary="NO SE REQUIERE EXPERIENCIA"
                        secondary="Obtendr&aacute;s formaci&oacute;n por parte de la empresa en distintas &aacute;reas y departamentos."
                    />
                </ListItem>
            </List>
        </Box>
    );
}

function FormRegistro({ candidateData, onCandidateData, inputRequired, onInputRequiredCh }) {
    const handleName = (e) => {
        const dataName = e.target.value;
        onCandidateData({
            name: dataName,
            phone: candidateData.phone,
            email: candidateData.email,
            age: candidateData.age
        });
        if (
            dataName !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && candidateData.age != ''
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handlePhone = (e) => {
        const dataPhone = e.target.value;
        onCandidateData({
            name: candidateData.name,
            phone: dataPhone,
            email: candidateData.email,
            age: candidateData.age
        });
        if (
            candidateData.name !== ''
            && dataPhone !== ''
            && candidateData.email !== ''
            && candidateData.age != ''
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handleEmail = (e) => {
        const dataEmail = e.target.value;
        onCandidateData({
            name: candidateData.name,
            phone: candidateData.phone,
            email: dataEmail,
            age: candidateData.age
        });
        if (
            candidateData.name !== ''
            && candidateData.phone !== ''
            && dataEmail !== ''
            && candidateData.age != ''
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    const handleAge = (e) => {
        const dataAge = e.target.value;
        onCandidateData({
            name: candidateData.name,
            phone: candidateData.phone,
            email: candidateData.email,
            age: dataAge
        });
        if (
            candidateData.name !== ''
            && candidateData.phone !== ''
            && candidateData.email !== ''
            && dataAge != ''
        ) {
            onInputRequiredCh(false);
        } else {
            onInputRequiredCh(true);
        }
    };

    return (
        <Box
            component="form"
            sx={{
                px: { sm: 1, md: '15%' }
            }}
            noValidate
            autoComplete="off"
        >
            <TextField
                id="input-name"
                label="Nombre y Apelidos"
                variant="standard"
                required
                fullWidth
                onChange={handleName}
                value={candidateData.name}
            />
            <TextField
                id="input-phone"
                label="Tel&eacute;fono M&oacute;vil"
                variant="standard"
                type="tel"
                required
                fullWidth
                onChange={handlePhone}
                value={candidateData.phone}
            />
            <TextField
                id="input-email"
                label="Correo Electr&oacute;nico"
                variant="standard"
                type="email"
                required
                fullWidth
                onChange={handleEmail}
                value={candidateData.email}
            />
            <TextField
                id="input-age"
                label="Edad"
                variant="standard"
                type="number"
                required
                fullWidth
                onChange={handleAge}
                value={candidateData.age}
                inputProps={{
                    inputMode: 'numeric',
                    pattern: '[0-9]*',
                    min: 18,
                    max: 60
                }}
            />
        </Box>
    );
}

function InfoPoliticaPriv() {
    return (
        <Box
            sx={{
                mx: '5%'
            }}
        >
            <Typography
                variant='body1'
                align='justify'
            >
                Le informamos que los datos personales que nos proporcione rellenando el presente formulario serán tratados por la empresa como responsable. Estos datos podrán ser usados únicamente para comunicaciones relacionadas a la candidatura de una oferta de empleo. Al presionar la opci&oacute;n de (ACEPTO), usted otorga su consentimiento expreso para la recogida y el tratamiento de sus datos personales exclusivamente con este fin de ofrecerle un empleo. Posterior a la candidatura serán TOTALMENTE eliminados.
            </Typography>
            <Typography
                variant='subtitle2'
                align='center'
                sx={{
                    mt: 2
                }}
            >
                Acepta nuestra pol&iacute;tica de privacidad?
            </Typography>
        </Box>
    );
}

export default function Registro({CompleteStep, onFinishStep}) {
    const [activeStep, setActiveStep] = React.useState(0);
    const [skipped, setSkipped] = React.useState(new Set());
    const [candidateData, setCandidateData] = React.useState({
        name: '',
        phone: '',
        email: '',
        age: ''
    });
    const [inputRequired, setInputRequired] = React.useState(true);

    const isStepSkipped = (step) => {
        return skipped.has(step);
    };

    const handleNext = () => {
        let newSkipped = skipped;
        if (isStepSkipped(activeStep)) {
            newSkipped = new Set(newSkipped.values());
            newSkipped.delete(activeStep);
        }

        setActiveStep((prevActiveStep) => prevActiveStep + 1);
        setSkipped(newSkipped);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        setActiveStep(0);
        setCandidateData({
            name: '',
            phone: '',
            email: '',
            age: ''
        });
    };

    const handleConfirm = () => {
        alert('Nombre: ' + candidateData.name
            + ' - Phone: ' + candidateData.phone
            + ' - Email: ' + candidateData.email
            + ' - Age: ' + candidateData.age
        );
        onFinishStep({...CompleteStep, registro: true});
    };

    return (
        <Box
            sx={{
                width: { sm: '90%', md: '60%' },
                height: '100%',
                px: '10%',
                pt: '5%',
                pb: '5%',
                my: { sm: 0, md: '5vh' },
                mx: { sm: 0, md: '10%' },
                boxShadow: { sm: 0, md: 1 },
            }}
        >
            <Box sx={{ width: '100%', mt: '2vh' }}>
                <Stepper activeStep={activeStep}>
                    {steps.map((label, index) => {
                        const stepProps = {};
                        const labelProps = {};
                        return (
                            <Step key={label} {...stepProps}>
                                <StepLabel {...labelProps}>{label}</StepLabel>
                            </Step>
                        );
                    })}
                </Stepper>
                <Box sx={{ pt: 5 }} />
                {activeStep === steps.length ? (
                    <React.Fragment>
                        <Typography
                            variant="h5"
                            align='center'
                            sx={{
                                mt: 2,
                                mb: 1
                            }}>
                            HA TERMINADO EL REGISTRO
                        </Typography>
                        <Typography
                            variant="subtitle1"
                            align='center'
                            sx={{
                                mt: 2,
                                mb: 1
                            }}>
                            Gracias por su tiempo.<br /> &#191;Podemos comenzar el cuestionario?
                        </Typography>
                        <Box sx={{ display: 'flex', flexDirection: 'row', py: '10vh' }}>
                            <Button
                                variant='outlined'
                                color="inherit"
                                disabled={activeStep === 0}
                                onClick={handleBack}
                                sx={{ mr: 1 }}
                            >
                                Regresar
                            </Button>
                            <Box sx={{ flex: '1 1 auto' }} />
                            <Button variant='outlined' onClick={handleConfirm}>Comenzar</Button>
                        </Box>
                    </React.Fragment>
                ) : (
                    <React.Fragment>
                        {(activeStep === 0) &&
                            <InfoInicial />
                        }
                        {(activeStep === 1) &&
                            <FormRegistro 
                                candidateData={candidateData}
                                onCandidateData={setCandidateData}
                                inputRequired={inputRequired}
                                onInputRequiredCh={setInputRequired}
                            />
                        }
                        {(activeStep === 2) &&
                            <InfoPoliticaPriv />
                        }
                        <Box sx={{ display: 'flex', flexDirection: 'row', py: '10vh' }}>
                            <Button
                                variant='outlined'
                                color="inherit"
                                disabled={activeStep === 0}
                                onClick={handleBack}
                                sx={{ mr: 1 }}
                            >
                                Regresar
                            </Button>
                            <Box sx={{ flex: '1 1 auto' }} />
                            {(activeStep === steps.length - 1) &&
                                <Button
                                    variant='outlined'
                                    color="inherit"
                                    onClick={handleReset}
                                >
                                    Cancelar
                                </Button>
                            }
                            <Button 
                                variant='outlined'
                                disabled={activeStep === 1 && inputRequired}
                                onClick={handleNext}
                            >
                                {activeStep === steps.length - 1 ? 'Acepto' : 'Siguiente'}
                            </Button>
                        </Box>
                    </React.Fragment>
                )}
            </Box>
        </Box>
    );
}